html{height:100%;}
html,body{min-height:100%;padding:0;margin:0;}

#index{position:relative;
    .playerPoker{position:relative;margin-top:40px;height:300px;
        .pokerContent{height:170px;position:relative;
            .pokerItem{display:line-block;width:100px;height:150px;position:absolute;background-color:#e8e8e8;border:1px solid #ddd;padding-top:6px;box-sizing:border-box;cursor:pointer;
                top:20px;
                .pokerColortext{position:absolute;bottom:6px;left:5px;display:block;width:18px;font-size:14px;text-align:center;}
                .pokerValue{margin-left:10px;

                }
            }
        }
    }
}

#login{display:table;width:100%;height:100%;
    .login_content{display:table-cell;vertical-align: middle;width:200px;height:200px;text-align:center;
        input{margin-top:20px;}
    }
}

.container{padding:0 16px;overflow:hidden;}
#hall{
    .header{height:40px;border-bottom:3px solid #fafafa;box-shadow: 0 0 19px -1px #fafafa;
        .left{width:50%;float:left;}
        .right{width:50%;float:left;text-align:right;height:40px;line-height: 40px;}
    }
    .hall_chat_fixed{position:fixed;width:500px;height:360px;padding-bottom:100px;box-sizing:border-box;border:1px solid #e8e8e8;box-shadow:0 0 3px 3px #fafafa;right:20px;bottom:80px;
        .chat_containter{height:100%;overflow: auto;border-bottom: 1px solid #e8e8e8;box-shadow: 0 1px 1px 2px #fafafa;box-sizing:border-box;padding:10px;
            .chat_content{padding-left:0;margin:0;
                li{list-style: none;padding-left:0;margin-bottom:10px;
                    .title{font-size:12px;color:#777;margin:0;padding-bottom:5px;
                        span{margin-left:6px;}
                    }
                    .info{color:#555;font-size:14px;padding-left:6px;}
                }
            }
        }
        .send_container{position:absolute;bottom:10px;left:0;width:100%;height:80px;padding:0 10px;box-sizing:border-box;
            textarea{width:390px;height:80px;box-sizing:border-box;border:1px solid #e8e8e8;resize: none;padding:5px 6px;}
            span{float:right;width:80px;height:80px;line-height:80px;box-sizing:border-box;border:1px solid #e8e8e8;text-align:center;box-shadow: 0 0 17px 2px #fafafa inset;cursor:pointer;}
        }
    }
}